.edition-badge {display:inline-block
}.edition-badge.is-block {display:block
}.edition-badge:not(.is-block) {position:relative;top:-1px
}.edition-badge.is-big .edition-badge-name {padding-left:0.75rem;padding-right:0.75rem;padding-top:0.25rem;padding-bottom:0.25rem;font-size:1rem;line-height:1.5rem
}.edition-badge .edition-badge-name {display:inline-block;border-radius:0.25rem;border-width:1px;border-style:solid;--tw-border-opacity:1;border-color:rgb(156 163 175 / var(--tw-border-opacity));padding-left:0.5rem;padding-right:0.5rem;padding-top:0px;padding-bottom:0px;font-size:0.75rem;line-height:1rem;text-transform:uppercase;--tw-text-opacity:1;color:rgb(107 114 128 / var(--tw-text-opacity));letter-spacing:1.5px
}