@charset "UTF-8";
@import "@craftcms/sass/mixins";

#main-container #main #main-content #content-container #content {
  display: flex;
  padding: 0;
  min-height: 400px;
}

.ps-wrapper {
  @apply tw-flex tw-flex-1 tw-flex-col;
  @apply lg:tw-flex-row;
  padding-bottom: 0 !important;

  .ps-sidebar {
    @apply tw-px-6 tw-py-6 tw-w-full;
    background-color: $grey050;
    @include border-radius($largeBorderRadius, 0, 0, $largeBorderRadius);
    @apply lg:tw-border-b-0 lg:tw-border-r lg:tw-overflow-auto lg:tw-w-64;


    .categories {
      @apply lg:tw-block;
    }
  }

  .ps-main {
    @apply tw-flex-1 tw-overflow-auto;

    .ps-container {
      @apply tw-flex-1 tw-max-w-screen-xl tw-mx-auto;
    }
  }
}

.ps-container {
  @apply tw-px-10 tw-py-6;
}